import { css } from "styled-components";

/**
 *      .
 */
const contentDirection = {
  /**
   *  
   * ```css
   * display: flex;
   * flex-direction: row;
   * ```
   */
  horizontal: css`
    display: flex;
    flex-direction: row;
  `,

  /**
   *  
   * ```css
   * display: flex;
   * flex-direction: column;
   * ```
   */
  vertical: css`
    display: flex;
    flex-direction: column;
  `,
} as const;

export default contentDirection;
